import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { DashboardComponent } from './dashboard.component';

const routes: Routes = [
  {
    path: '',
    component: DashboardComponent,
    children:[
      {
        path: 'overviews',
        loadChildren: () => import('./overviews/overviews.module').then(m => m.OverviewsModule)
      },
      {
        path: 'payment',
        loadChildren: () => import('./payment/payment.module').then(m => m.PaymentModule)
      },
      {
        path: 'extended-use',
        loadChildren: () => import('./extended-use/extended-use.module').then(m => m.ExtendedUseModule)
      },
      {
        path: 'history-payment',
        loadChildren: () => import('./history-payment/history-payment.module').then(m => m.HistoryPaymentModule)
      },
      {
        path: 'manage-keys',
        loadChildren: () => import('./manage-keys/manage-keys.module').then(m => m.ManageKeysModule)
      },
    ]
  },
  

]



@NgModule({
  declarations: [DashboardComponent],
  imports: [
    CommonModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    NgZorroAntModule,
    TranslateModule,
    RouterModule.forChild(routes)
  ]
})
export class DashboardModule { }
